.description-container {
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0;
	right: 0;
	background: #0f0c4a;
	clip-path: circle(50px at 0 0);
	transition: all 0.5s;
	cursor: pointer;
}
.description {
	position: absolute;
	top: 50%;
	left: 50%;
	margin: 0;
	text-align: center;
	font-size: 10px;
	text-transform: uppercase;
	color: #fff;
	transform: translate(-50%, -50%);
	width: 100%;
	white-space: nowrap;
	overflow-wrap: break-word;
}

.description p {
	white-space: initial;
	padding-left: 5px;
	padding-right: 5px;
}
.description h5 {
	white-space: initial;
	padding-left: 5px;
	padding-right: 5px;
}
.description-container:hover {
	border-radius: 10px;
	clip-path: circle(400px at 0 0);
}

